.card__box {
  position: relative;
  overflow: hidden; }
  .card__box--image {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    border: 0;
    border-radius: 0; }
    .card__box--image img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      transform: scale(1.01);
      transition: all 0.2s ease-in-out; }
  .card__box--body {
    position: relative;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    transition: all 0.2s ease-in-out; }
    @media (min-width: 768px) {
      .card__box--body {
        min-height: 400px; } }
    .card__box--body:before {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      display: block;
      background: #424242;
      opacity: .5; }
  .card__box--title {
    color: #fff;
    font-weight: 700;
    display: block;
    display: -webkit-box;
    overflow: hidden;
    -webkit-box-orient: vertical;
    max-width: 100%;
    text-overflow: ellipsis;
    font-size: 1.5rem;
    line-height: 1.5;
    -webkit-line-clamp: 2;
    white-space: normal;
    max-height: 4.5rem; }
  @media (min-width: 992px) {
    .card__box:hover .card__box--image img {
      transform: scale(1.05); } }

